* {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
html,
body,
.container-fluid {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
html,
body {
  text-align: center;
  width: 100%;
  height: 100%;
}
.container-fluid {
  text-align: left;
  margin: auto;
  max-width: 1024px;
  height: 100%;
}
@media (min-width: 1200px) {
  body {
    background-color: #f5f5f5;
    padding: 40px 0;
    background-image: none;
  }
  body .container-fluid {
    border: 1px solid #e5e5e5;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
    -moz-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  }
}
h1,
h2,
h3 {
  font-family: 'UbuntuTitlingBold';
}
.masthead {
  margin-top: 25px;
}
footer {
  margin-bottom: 20px;
}
.center {
  text-align: center;
}
.center > * {
  margin: auto;
}
.round.btn {
  border-radius: 500px;
  width: 200px;
  height: 200px;
  padding: 0;
  margin: auto;
  border: 5px solid lightgrey;
  box-shadow: 0 0 10px lightgrey;
}
.round.btn:hover {
  border: 5px solid white;
  box-shadow: 0 0 15px grey;
}
.round.btn:active {
  box-shadow: 0 0 10px grey;
}
.round.btn span {
  line-height: 200px;
  vertical-align: middle;
}
.vertex {
  display: inline-block;
  text-align: center;
  width: 80px;
  height: 80px;
  line-height: 80px;
  border: 1px solid #ccc;
  -webkit-border-radius: 80px;
  -moz-border-radius: 80px;
  border-radius: 80px;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  background-color: white;
}
.vertex span {
  white-space: nowrap !important;
  width: 80px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
}
.vertex.answer {
  background-color: lightpink;
}
.vertex.selected {
  border: 1px solid red;
}
.vertex.blue {
  background-color: lightblue;
}
.vertex.deleted {
  background-color: red;
  display: none;
}
canvas {
  position: absolute;
  text-align: left;
  width: 100%;
  height: 100%;
  top: 200px;
  left: 0;
  z-index: 0;
}
.vertices {
  position: absolute;
  text-align: left;
  width: 100%;
  height: 100%;
  top: 200px;
  left: 0;
  z-index: 10;
}
.vertices .vertex {
  position: absolute !important;
  z-index: 100;
}
.vertices .vertex:hover {
  color: darkred;
  text-decoration: underline;
}
.vertices .vertex span {
  white-space: nowrap !important;
  width: 80px;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
}
.vertices .vertex input {
  width: 100%;
}
.vertices .vertex.ui-state-active {
  border: 1px solid red;
}
.vertices canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
}
#play-view {
  height: 100%;
  position: relative;
  text-align: center;
  overflow: hidden;
}
#play-view .questions {
  position: relative;
  z-index: 1000;
}
#play-view .questionBlock {
  margin-top: 1em;
}
#play-view .questionBlock p {
  margin-bottom: 1em;
}
#play-view .questionBlock .question {
  display: none;
}
#play-view .questionBlock .question h1 {
  font-size: 32px;
}
#play-view .questionBlock .question p,
#play-view .questionBlock .question input,
#play-view .questionBlock .question select,
#play-view .questionBlock .question option {
  font-size: 16px;
}
#play-view .questionBlock .vertex + .vertex {
  margin-left: 10px;
}
#play-view .questionBlock .page {
  background-color: rgba(255, 255, 255, 0.7);
}
#play-view .menu {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 200;
  width: 100%;
}
#play-view #you-wrapper {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 100;
}
#play-view #you-wrapper > div {
  position: relative;
}
#play-view #you-wrapper > div #you {
  position: absolute;
  top: -20px;
  right: -115px;
  display: none;
}
#ss-form {
  display: none;
}
